main {
  z-index: 2;
}

//sticky menu
.scrolled .blog-menu {
  padding: 0px !important;
  box-shadow: 0 1px 2px 0 rgba(34, 34, 34, .149);
  top: 0;
}

.single-post, .blog {
  .position-custom {
    background-color: #74489d;
    position: fixed !important;
    left: 0 !important;
    right: 0 !important;
  }
}

//progress bar
.blog-progress {
  visibility: hidden;
  opacity: 0;
  @extend .lazy;
}

.blog-progress.show {
  visibility: visible;
  opacity: 1;
}

.scrolled .blog-progress {
  top: 55px;
}

progress {
  height: 0.6rem;
  background: #41af78;
}

progress {
  color: lightblue;
}

progress::-moz-progress-bar {
  background: lightblue;
}

progress::-webkit-progress-value {
  background: #41af78;
}

progress::-webkit-progress-bar {
  background: $success;
}

//mobile menu
@media only screen and (max-width: 992px) {

}

.modal-dialog {
  pointer-events: auto !important;
}

//hamburger
.menu-toggle {
  z-index: 1000;
  transition: margin .5s ease;
  cursor: pointer;
  left: 10px;
  top: 15px;
  width: 42px;
  height: 42px;

  .lines-button {
    width: 42px;
    height: 42px;
  }

  .lines {
    width: 24px;
    height: 2px;
    -webkit-text-fill-color: #fff;

    &:before,
    &:after {
      content: '';
      height: 2px;
      width: 24px;
      position: absolute;
      left: 0;
      transform-origin: 1.5px center;
      transition: transform .3s;
      display: inline-block;
      background-color: #fff;
    }

    &:before {
      top: 8px;
    }

    &:after {
      top: -8px;
    }
  }

  &:not(.active) .lines-button:hover .lines:before {
    transform: translateY(-2px);
  }

  &:not(.active) .lines-button:hover .lines:after {
    transform: translateY(2px);
  }
}

//toggle close
.menu-toggle-close {
  left: 10px;
  top: 15px;
  width: 42px;
  height: 42px;
  padding: 0;
}

.lines-button-close {
  cursor: pointer;
  width: 42px;
  height: 42px;

  .lines-close {
    width: 24px;
    height: 3px;
    background: none;

    &:before,
    &:after {
      transform-origin: 50% 50%;
      top: 0;
      content: '';
      height: 3px;
      width: 24px;
      position: absolute;
      left: 0;
      display: inline-block;
      background-color: #fff;
    }

    &:before {
      transform: rotate3d(0, 0, 1, 45deg);
      transition: transform .2s ease;
    }

    &:after {
      transform: rotate3d(0, 0, 1, -45deg);
      transition: transform .2s ease;
    }
  }

  &:hover .lines-close:before,
  &:hover .lines-close:after {
    -ms-transform: rotate3d(0, 0, 1, 0deg);
    -webkit-transform: rotate3d(0, 0, 1, 0deg);
    transform: rotate3d(0, 0, 1, 0deg);
  }
}

